<template>
  <div>
    <div
      ref="canvasContainer"
      style="scale: 0.78;position: absolute;margin-top: -60px;margin-left: -50px;max-width: 100%;height: 100vh;"
    >
      <b-button
        variant="success"
        @click="BigData"
      >
        
      </b-button>

    </div>
    <div
      id="container"
      class="div"
    >
      
    </div>
    <div
      id="container"
      class="model"
    >
      3D
    </div>
  </div>

</template>

<script>
import * as THREE from 'three'

import { STLLoader } from 'three/examples/jsm/loaders/STLLoader'
// import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls' // 
import {
  BButton,
} from 'bootstrap-vue'
import background from '@/assets/images/gif/1.gif' // 

export default {
  components: {
    BButton,
  },
  data() {
    return {
      scene: null,
      camera: null,
      renderer: null,
      mesh: null,
    }
  },
  beforeDestroy() {
    // 
    if (!this.myChart) {
      return
    }
    this.myChart.dispose()
    this.myChart = null
  },
  mounted() {
    // 1
    this.scene = new THREE.Scene()
    // this.scene.background = new THREE.Color(0x38628a) // 

    const loader = new THREE.TextureLoader()
    const backgroundTexture = loader.load(background)

    // 
    backgroundTexture.minFilter = THREE.LinearFilter
    this.scene.background = backgroundTexture

    // 2
    this.camera = new THREE.PerspectiveCamera(
      30, window.innerWidth / window.innerHeight,
      0.1,
      750,
    )
    this.camera.position.z = 420 //  z 
    this.camera.position.x = 200 //  x 
    this.camera.position.y = 100 //  y 

    // 3
    this.renderer = new THREE.WebGLRenderer()
    this.renderer.setSize(window.innerWidth, window.innerHeight)
    this.$refs.canvasContainer.appendChild(this.renderer.domElement)
    this.renderer.antialias = true

    // 
    window.addEventListener('resize', () => {
      const newWidth = window.innerWidth
      const newHeight = window.innerHeight
      this.camera.aspect = newWidth / newHeight
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(window.innerWidth, window.innerHeight)
    })

    // 
    const ambientLight = new THREE.AmbientLight(0xffffff, 1) // 
    this.scene.add(ambientLight)
    // 
    const light = new THREE.DirectionalLight(0xffffff, 4.2)
    light.position.set(2, 2, 2).normalize()
    this.scene.add(light)
    // 
    const light2 = new THREE.DirectionalLight(0xffffff, 2)
    light2.position.set(-2, -2, -2).normalize() // 
    this.scene.add(light2)

    if (this.renderer.domElement) {
      // OrbitControls
      const controls = new OrbitControls(this.camera, this.renderer.domElement)

      // 
      controls.enableDamping = true // 
      controls.dampingFactor = 0.1 // 
      controls.rotateSpeed = 0.5 // 

      const tloader = new STLLoader()
      // STLSTL
      tloader.load('model/tong.stl', geometry2 => {
        const material2 = new THREE.MeshPhongMaterial({
          color: 0xcecece,
          side: THREE.DoubleSide,
          shininess: 100,
          specular: 0x111111,
        })

        const mesh2 = new THREE.Mesh(geometry2, material2)
        mesh2.scale.set(0.15, 0.15, 0.15)
        mesh2.position.set(380, 20, 0) // STL

        this.scene.add(mesh2)

        let rotationAngle = 0 // 
        const animate = () => {
          rotationAngle += 0.01 // 
          mesh2.rotation.set(0, rotationAngle, 0)// 
          requestAnimationFrame(animate)
          controls.update() // 
          this.renderer.render(this.scene, this.camera)
        }

        animate() // 

        const boundingBox = new THREE.Box3().setFromObject(mesh2)
        const center = new THREE.Vector3()
        boundingBox.getCenter(center)
        controls.target.copy(center) // 
      })

      // STL
      // const loader = new FBXLoader()
      const sloader = new STLLoader()
      // STL
      sloader.load('model/2.stl', geometry => {
        // MeshLambertMaterial 
        const material = new THREE.MeshPhongMaterial({ // Phong material 
          color: 0xcecece, // 
          side: THREE.DoubleSide,
          shininess: 100, // 
          specular: 0x111111, // 
        })

        const mesh = new THREE.Mesh(geometry, material)
        mesh.scale.set(0.05, 0.05, 0.05) // 
        mesh.position.set(200, 20, 0) // STL
        let rotationAngle = 0 // 
        const animate = () => {
          rotationAngle += 0.005 // 
          mesh.rotation.set(0, rotationAngle, 0)// 
          requestAnimationFrame(animate)
          controls.update() // 
          this.renderer.render(this.scene, this.camera)
        }

        animate() // 

        const boundingBox = new THREE.Box3().setFromObject(mesh)
        const center = new THREE.Vector3()
        boundingBox.getCenter(center)
        controls.target.copy(center) // 

        this.scene.add(mesh)
      })

      window.addEventListener('resize', () => {
        controls.update()
      })

      // 
    }
  },
  methods: {
    BigData() {
      //  Vue Router  Home 
      window.location.href = 'BigData.vue'
    },
  },
}

</script>
